import axios from 'axios';
axios.defaults.baseURL = 'http://192.168.1.8:3999/';
axios.interceptors.response.use(function (response) {
  // 
  return response;
}, function (error) {
  // 
  return Promise.reject(error);
});
export default{
  install(Vue,options){
    Vue.prototype.$axios=axios;
  }
}
export  const $axios=axios;


